:root {
  --font-text: "Epilogue", sans-serif;
  --color-correct: rgb(118, 255, 180);
  --color-misplaced: rgb(255, 174, 0);

  --size-text: 18px;

  --color-background: rgb(238, 232, 212);
  --color-text: #444;
  --color-border: #666;

  --color-palette-blue: rgb(170, 209, 253);
  --color-palette-green: rgb(211, 255, 231);
  --color-palette-yellow: rgb(255, 250, 210);
  --color-palette-red: rgb(255, 220, 220);
  --color-palette-pink: rgb(255, 214, 250);
}

@media (prefers-color-scheme: dark) {
  :root {
    --color-background: rgb(58, 58, 58);
    --color-text: rgb(255, 255, 255);
    --color-border: rgb(226, 226, 226);
  }
}
